.ng2-c-chart--vertical-bar {
  min-height: 235px; }
  .ng2-c-chart--vertical-bar .axis.x .domain,
  .ng2-c-chart--vertical-bar .axis.y .domain {
    stroke: none; }
  .ng2-c-chart--vertical-bar .major-ticks-x line,
  .ng2-c-chart--vertical-bar .minor-ticks-x line {
    stroke: none; }
  .ng2-c-chart--vertical-bar .axis.y path + g line {
    stroke: #cccccc; }
  .ng2-c-chart--vertical-bar path {
    fill: none;
    stroke: #28648a;
    stroke-width: 1.5; }
  .ng2-c-chart--vertical-bar .axis line {
    fill: none;
    stroke: #f5f5f5;
    stroke-width: 1; }
  .ng2-c-chart--vertical-bar .axis.x line {
    visibility: hidden; }
  .ng2-c-chart--vertical-bar .axis.y {
    fill: #9b9b9b; }
    .ng2-c-chart--vertical-bar .axis.y .tick-base line {
      stroke: #cccccc; }

.ng2-c-bar-chart__bar {
  fill: #28648a; }

.ng2-c-bar-chart__fake-bar-border {
  fill: #28648a;
  fill-opacity: 0.25;
  stroke: none; }

.ng2-c-bar-chart__label {
  color: #28648a;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 18px;
  margin-bottom: 5px;
  text-transform: uppercase; }

.ng2-c-bar-chart__value {
  color: #4a4a4a;
  font-size: 18px;
  line-height: 22px; }
